* {
  box-sizing: border-box;
}
.game {
  display: flex;
  padding: 30px;
}

.frame {
  display: flex;
  flex-wrap: wrap;
  width: 242px;
  height: 242px;
  border: 1px solid;
  background-color: rgb(122, 122, 223);
}

.frame div {
  display: flex;
  border: 1px solid;
  width: 80px;
  height: 80px;
  background-color: aqua;
  font-size: 60px;
  justify-content: center;
  align-items: center;
}

@media (min-width: 400px) {
  .game {
    display: flex;
    padding: 30px;
  }

  .frame {
    display: flex;
    flex-wrap: wrap;
    width: 302px;
    height: 302px;
    border: 1px solid;
    background-color: rgb(122, 122, 223);
  }

  .frame div {
    display: flex;
    border: 1px solid;
    width: 100px;
    height: 100px;
    background-color: aqua;
    font-size: 80px;
    justify-content: center;
    align-items: center;
  }
}

@media (min-width: 580px) {
  .game {
    display: flex;
    padding: 30px;
  }

  .frame {
    width: 604px;
    height: 604px;
    border: 2px solid;
  }

  .frame div {
    border: 1px solid;
    width: 200px;
    height: 200px;
    font-size: 200px;
  }
}
